<template>
  <div class="h-full min-h-[500px] w-full">
    <GeneralForm
      :heading="'Follow Ups'"
      :sub-heading="companyName"
      :showCloseButton="true"
      @close-pressed="handleClose"
      :show-submit-button="false"
    >
      <template #form-body>
        <div class="flex items-center gap-spacing-100">
          <p class="text-body/l/relaxed/400 text-content-on-neutral-secondary">
            Status:
          </p>
          <p class="text-body/l/relaxed/400 text-content-on-neutral-primary">
            {{ status }}
          </p>
        </div>
        <div class="flex items-center gap-spacing-100">
          <p class="text-body/l/relaxed/400 text-content-on-neutral-secondary">
            Next Follow Up:
          </p>
          <p class="text-body/l/relaxed/400 text-content-on-neutral-primary">
            {{ nextFollowUpDate | toDate }}
          </p>
        </div>
        <PaginatedTable
          :headers="followUpHeaders"
          :current-page="currentPage"
          :total-pages="totalPages"
          :sort-key="sortKey"
          :sort-order="sortOrder"
          :total-count="totalCount"
          :error="error"
          @fetch-sorted-data="fetchSortedData"
          @fetch-data="fetchData"
          :horizontal-padding="'px-spacing-0'"
        >
          <template #table-body>
            <tr
              v-for="followUp in followUpsData"
              :key="followUp?._id"
              class="text-left text-body/m/relaxed/400 text-content-on-neutral-primary nexus:text-body/l/relaxed/400"
            >
              <td class="gap-spacing-25 py-spacing-100 pr-spacing-100">
                {{ followUp?.date | toDate }}
              </td>
              <td class="py-spacing-100 pr-spacing-100">
                {{ followUp?.note || "-------------------" }}
              </td>
            </tr>
            <tr
              v-show="isLoading"
              v-for="(_, index) in new Array(4).fill('')"
              :key="index"
              class="text-left"
            >
              <td
                v-for="(_, idx) in new Array(2).fill('')"
                :key="idx"
                class="gap-spacing-25 py-spacing-100 pr-spacing-100"
              >
                <GeneralSkeleton
                  :is-table-cell="true"
                  :class="
                    idx == 1
                      ? 'h-spacing-300 w-[200px] ipad:w-[500px]'
                      : 'h-spacing-300 w-spacing-1200'
                  "
                />
              </td>
            </tr>
          </template>
        </PaginatedTable>
      </template>
    </GeneralForm>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { debounce } from "lodash";
export default {
  props: {
    companyId: {
      type: String,
      required: true,
    },
    marketResearchId: {
      type: String,
      required: true,
    },
  },
  components: {
    PaginatedTable: () =>
      import("@/components/General/Table/PaginatedTable.vue"),
    GeneralForm: () => import("@/components/General/Form.vue"),
    GeneralSkeleton: () => import("@/components/General/Skeleton.vue"),
  },
  data() {
    return {
      sortKey: "",
      sortOrder: "",
      limit: 10,
      followUpHeaders: [
        { key: "updateDate", value: "Last Update", sortable: true },
        { key: "note", value: "Note", sortable: false },
      ],
      isLoading: false,
      fetchDebouncedData: debounce(this.fetchData, 300),
      currentPage: 0,
    };
  },
  mounted() {
    this.fetchData();
  },
  computed: {
    ...mapState({
      followUpsData: (state) =>
        state.marketResearch.followUpsByCompany?.followUpHistory,
      companyName: (state) =>
        state.marketResearch.followUpsByCompany?.company?.companyName,
      currentPageNumber: (state) =>
        state.marketResearch.followUpsByCompany?.currentPage || 0,
      totalPages: (state) =>
        state.marketResearch.followUpsByCompany?.totalPages || 1,
      totalCount: (state) =>
        state.marketResearch.followUpsByCompany?.totalCount || 0,
      nextFollowUpDate: (state) =>
        state.marketResearch.followUpsByCompany?.nextFollowUpDate,
      status: (state) => state.marketResearch.followUpsByCompany?.status,
      error: (state) => state.marketResearch?.error,
    }),
  },
  methods: {
    ...mapActions("marketResearch", ["getAllFollowUpsByCompany"]),

    async fetchData(page = 1, resolve) {
      this.isLoading = true;
      this.currentPage = page;
      let payload = {
        params: {
          marketResearchId: this.marketResearchId,
          companyId: this.companyId,
          sortBy: this.sortKey || undefined,
          sortOrder: this.sortOrder || undefined,
          page: page,
          limit: this.limit,
        },
      };
      await this.getAllFollowUpsByCompany(payload);
      this.currentPage = this.currentPageNumber;
      this.isLoading = false;
      if (resolve) {
        resolve();
      }
    },
    fetchSortedData(options) {
      this.sortKey = options?.sortKey || "";
      this.sortOrder = options?.sortOrder || "";
      this.fetchDebouncedData();
    },
    handleClose() {
      this.$bus.$emit("more-follow-ups-modal", false);
    },
  },
};
</script>
